.recent-articles__like,
.recent-articles__comment {
  height: 17px;
  padding-left: 20px;
}
.recent-article-wr {
  position: relative;
  max-width: 363px;
  display: flex;
  flex-direction: column;
}
.recent-article-wr::before,
.recent-article-wr::after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 7px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  transform: rotate(-3deg);
}
.recent-article-wr::after {
  transform: rotate(3deg);
  right: 7px;
  left: auto;
}
.recent-articles__banner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80px;
  width: calc(100% - 14px);
  margin-left: 14px;
  background-image: linear-gradient(to bottom, #f4f3f3, #f4f3f3), linear-gradient(to bottom, #000000, #ffffff);
}
.recent-articles__banner span {
  font-family: Arial, sans-serif;
  font-size: 12px;
  color: #636363;
  text-shadow: 0px 1px 0 rgba(255, 255, 255, 0.75);
}
.recent-articles__banner::before,
.recent-articles__banner::after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 21px;
  left: 7px;
  width: 50%;
  top: 68%;
  max-width: 740px;
  background: #000;
  box-shadow: 0 15px 10px #000;
  transform: rotate(-3deg);
}
.recent-articles__banner::after {
  transform: rotate(3deg);
  right: 7px;
  left: auto;
}
.recent-articles__like {
  margin-right: 10px;
}
@media screen and (max-width: 970px) {
  .sidebar,
  .main-content {
    float: none;
    max-width: 100%;
  }
  .sidebar {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .recent-articles__row {
    padding-right: 13px;
  }
  .recent-articles__banner {
    margin-right: 14px;
    width: calc(100% - 28px);
  }
  .main-article__content-row {
    max-width: 100%;
  }
  .main-article__avatar-wr {
    margin-bottom: 15px;
  }
  .about-author,
  .related-post,
  .table-currency {
    margin-right: 15px;
    margin-left: 15px;
  }
}
@media screen and (max-width: 817px) {
  .recent-articles__row {
    flex-direction: column;
    align-items: center;
  }
  .recent-article-wr {
    margin-bottom: 15px;
  }
}
@media screen and (max-width: 420px) {
  .sidebar {
    flex-direction: column;
  }
}
.map {
  padding-top: 12px;
}
.map iframe {
  display: block;
  width: 100%;
  height: 200px;
}
.map-border-two {
